import {Component, EventEmitter, Injectable, Input, OnInit, Output, ViewEncapsulation} from '@angular/core';
import {NgbDateNativeAdapter, NgbDatepickerI18n, NgbDateStruct} from '@ng-bootstrap/ng-bootstrap';
import {FormGroup} from '@angular/forms';
import {Icons} from '@models/constants/icons';

const I18N_VALUES = {
    ru: {
        weekdays: ['Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб', 'Вс'],
        months: ['Янв', 'Фев', 'Мар', 'Апр', 'Май', 'Июн', 'Июл', 'Авг', 'Сен', 'Окт', 'Ноя', 'Дек'],
    },
    kk: {
        weekdays: ['Дс', 'Сс', 'Ср', 'Бс', 'Жм', 'Сб', 'Жк'],
        months: ['Қаң', 'Ақп', 'Нау', 'Сәу', 'Мам', 'Мау', 'Шіл', 'Там', 'Қыр', 'Қаз', 'Қар', 'Жел'],
    }
};

@Injectable()
export class CustomDatepickerI18n extends NgbDatepickerI18n {

    constructor() {
        super();
    }

    getWeekdayShortName(weekday: number): string {
        return I18N_VALUES[localStorage.getItem('lang')].weekdays[weekday - 1];
    }

    getMonthShortName(month: number): string {
        return I18N_VALUES[localStorage.getItem('lang')].months[month - 1];
    }

    getMonthFullName(month: number): string {
        return this.getMonthShortName(month);
    }

    getDayAriaLabel(date: NgbDateStruct): string {
        return `${date.day}-${date.month}-${date.year}`;
    }
}

@Component({
    selector: 'app-datepicker',
    templateUrl: './datepicker.component.html',
    styleUrls: ['./datepicker.component.scss'],
    encapsulation: ViewEncapsulation.None,
    providers: [{provide: NgbDatepickerI18n, useClass: CustomDatepickerI18n}]
})

export class DatepickerComponent implements OnInit {
    public icons = Icons;
    public isVisible = false;
    public minDate: NgbDateStruct = {year: 1900, month: 1, day: 1};
    public maxDate: NgbDateStruct = {year: 2100, month: 1, day: 1};
    @Input() submitted: boolean;
    @Input() controlName: string;
    @Input() form: FormGroup;
    @Input() fieldValue: string;
    @Output() setControlValue: EventEmitter<string> = new EventEmitter<string>();

    constructor(private dateAdapter: NgbDateNativeAdapter) {
    }

    ngOnInit() {
    }

    onDateSelected(value) {
        const date = this.dateAdapter.toModel(value);
        this.setControlValue.emit(date.toISOString());
        this.onToggle();
    }

    onToggle() {
        this.isVisible = !this.isVisible;
    }

}
